import React, { Component } from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
class InActiveImageGrid extends Component {
  render() {
    const sortingOptions = {
      defaultSortName: "ImageName",
      noDataText: "No records found for Images",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.imageFiles.length
        }
      ],
      sizePerPage: 5
    };
    return (
      <BootstrapTable
        ref="table"
        data={this.props.imageFiles}
        pagination={this.props.imageFiles.length > 0 ? true : false}
        search={true}
        options={sortingOptions}
        hover={true}
      >
        <TableHeaderColumn dataField="Id" headerAlign="left" isKey hidden>
          Id
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="ImageName"
          headerAlign="left"
          width="20"
          dataSort={true}
        >
        Image Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="FilePath"
          dataFormat={this.props.showImage}
          headerAlign="left"
          width="20"
        >
          Image
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="delete"
          dataFormat={this.props.onDeleteImage}
          headerAlign="left"
          width="20"
        >
          Activate
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

export default InActiveImageGrid;



