import React, { Component } from "react";
import { Col, FormGroup, Row } from "reactstrap";
import CardLayout from "../../components/Cards/CardLayout";
import Loader from "../../components/Loader/Loader";
import * as actions from "../../store/actions";
import { connect } from "react-redux";
import ImageCards from "../../components/Cards/ImageCards";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import ActiveImageGrid from "./ActiveImageGrid";
import InActiveImageGrid from "./InActiveImageGrid";
import ConfirmModal from "../../components/Modal/ConfirmModal";
import _ from "lodash";
import { Link } from "react-router-dom";
import DropdownSelect from "../../components/InputElement/Dropdown";
import * as constants from "../../constants/StatusConstants";

class ImageContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      showGridView: false,
      modalStatus: false,
      imageToDelete: "",
      tableStatus: true
    };
  }
  componentDidMount() {
    this.props.getImageFiles();
    let compRef = this;
    setTimeout(() => {
      compRef.setState({ loading: false });
      if (compRef.props.imageError) {
        Toaster.Toaster("Something went wrong !", compRef.props.imageError);
      }
    }, 2000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  showImage(cell, row) {
    return <img src={row.FilePath} style={{ height: 50, width: 50 }} alt="" />;
  }
  toggleView() {
    this.setState({
      showGridView: !this.state.showGridView
    });
  }
  onDeleteImage(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.confirmDelete(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.confirmDelete(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  confirmDelete(image) {
    this.setState({
      imageToDelete: image
    });
    this.onModalToggle();
  }
  onModalToggle() {
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let imageToDelete = { ...this.state.imageToDelete };
    imageToDelete.FilePath = imageToDelete.ServerPath;
    let image = _.pick(imageToDelete, [
      "Id",
      "ImageName",
      "FilePath",
      "Active"
    ]);
    let displayMessage = "";
    if (this.state.tableStatus) {
      image.Active = false;
      displayMessage = "Image deactivated successfully";
    } else {
      image.Active = true;
      displayMessage = "Image activated successfully";
    }
    this.props.deleteImageFile(image.Id, image);
    this.setState({ loading: true });
    setTimeout(() => {
      let message = "";
      this.props.imageError
        ? (message = this.props.imageError)
        : (message = displayMessage);
      this.setState({ loading: false });
      Toaster.Toaster(message, this.props.imageError);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    let imageCards = this.props.imageFiles.map((media, idx) => {
      return (
        <Col xs="12" md="4" key={idx}>
          <ImageCards
            link={this}
            onDelete={() => this.confirmDelete(media)}
            imageName={media.ImageName}
            id={idx}
            source={media.FilePath}
          />
        </Col>
      );
    });
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Images"
        buttonName="Add Image"
        buttonLink={`${this.props.match.url}/imageUpload`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveImageGrid
                imageFiles={this.props.activeImages}
                showImage={this.showImage.bind(this)}
                onDeleteImage={this.onDeleteImage.bind(this)}
              />
            ) : (
              <InActiveImageGrid
                imageFiles={this.props.inActiveImages}
                showImage={this.showImage.bind(this)}
                onDeleteImage={this.onDeleteImage.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this image ?"
              : "Are you sure you want to activate this image ?"
          }
        />
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}
const mapStateToProps = state => {
  return {
    imageFiles: state.mediaReducer.activeImages,
    imageError: state.mediaReducer.imageError,
    activeImages: state.mediaReducer.activeImages,
    inActiveImages: state.mediaReducer.inActiveImages
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getImageFiles: () => dispatch(actions.getImageFiles()),
    deleteImageFile: (id, image) => dispatch(actions.deleteImageFile(id, image))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ImageContent);



